













































































import {Component, Vue} from "vue-property-decorator";

@Component
export default class CnRoutes extends Vue{
    showServicesMenu = false;
    showResources = false;
    showCompany = false;

    closeMenuPhone(){
        this.$emit('closeMenuPhone');
    }

    get upDownServiceMenu(){
        return{
            rotateArrow: this.showServicesMenu
        }
    }

    get upDownResourceMenu(){
        return{
            rotateArrow: this.showResources
        }
    }

    get upDownCompanyMenu(){
        return{
            rotateArrow: this.showCompany
        }
    }
}
