







import {Component, Vue, Prop} from "vue-property-decorator";

@Component
export default class CnInmageTitle extends Vue {
    @Prop() imgLogoExtension!: string;
    @Prop() title!: string;
    @Prop() onLeft!: boolean;

    get fullPath(){
        return require(`@/assets/services/${this.imgLogoExtension}`);
    }

    get cssTitle(){
        return{
            titleLeft: this.onLeft,
            titleRight: !this.onLeft
        }
    }

    get cssImg(){
        return{
            imgLeft: this.onLeft,
            imgRight: !this.onLeft
        }
    }
}
