









import {Component, Vue} from "vue-property-decorator";
import CnHamburger from "@/components/commons/CnHamburger.vue";
import CnMenuPhone from "@/components/commons/CnMenuPhone.vue";
import CnMenuDesktop from "@/components/commons/CnMenuDesktop.vue";
@Component({
    components: {CnMenuDesktop, CnMenuPhone, CnHamburger}
})
export default class CnNavbar extends Vue {
    showMenuPhone = false;

    switchMenu(){
        this.showMenuPhone = !this.showMenuPhone;
    }

    clickOnLogo(){
        this.showMenuPhone = false;
        this.$router.push('home');
    }
}
