













import {Component, Vue, Prop} from "vue-property-decorator";

@Component
export default class CnArrowList extends Vue {
    @Prop() link!: string;
    @Prop() imgNameExtension!: string;
    @Prop() company!: string;
    @Prop() catchPhrase!: string;

    get fullPath(){
        return require(`@/assets/${this.imgNameExtension}`);
    }
}
