






























































































import {Component, Vue, Watch} from "vue-property-decorator";
import CnTitle from "@/components/commons/CnTitle.vue";
import axiosCn from "@/axios/axiosCn";
import Invitation from "@/models/Invitation";
import GuestForm from "@/models/GuestForm";
import CnResumeInvitation from "@/components/CnResumeInvitation.vue";
@Component({
    components: {CnResumeInvitation, CnTitle}
})
export default class CnEvent extends Vue{
    mainGuest: GuestForm = new GuestForm();
    company = null;
    guestList: GuestForm[] = [];
    guestNumber = 1;
    sendingForm = false;
    disabledSending = false;

    created(){
        for(let i = 0; i < this.guestNumber; ++i){
            this.guestList.push(new GuestForm());
        }
    }

    @Watch('guestNumber')
    onGuestNumberChange(newValue: number, oldValue: number){
        if(newValue > oldValue){
            for(let i = oldValue; i < newValue; ++i)
                this.guestList.push(new GuestForm());
        }
        else
            this.guestList.length = newValue;
    }

    async submitInscription(){
        try {
            this.disabledSending = true;
            if(this.checkForm()) {
                const invitation = new Invitation(this.company, this.mainGuest, this.guestList);
                const response = await axiosCn.post('/cn/event', {invitation});
                if(response.status === 200){
                    this.sendingForm = true;
                }
            }
            else{
                alert('Veuillez remplir tout le formulaire');
            }
        } catch (error) {
            //console.error(error);
        } finally {
            this.disabledSending = false;
        }
    }

    checkForm(): boolean{
        const errorInMainGuest = !this.mainGuest.checkGuest();
        const otherGuestCheck: boolean[] = [];
        this.guestList.forEach(guest => {
            otherGuestCheck.push(guest.checkGuest())
        });
        let errorInGuestList = false;
        otherGuestCheck.forEach(isCheck => {
            if (!isCheck){
                errorInGuestList = true;
            }
        });
        return !errorInMainGuest && !errorInGuestList;
    }
}
