















import {Component, Prop, Vue} from "vue-property-decorator";
import CnGalleryTitle from "@/components/gallery/CnGalleryTitle.vue";
import {Galleries} from "@/enums/Galleries";

@Component({
    components: {CnGalleryTitle}
})

export default class CnGalleryDetail extends Vue{
    @Prop() title!: string;
    @Prop() enumGallery!: Galleries;
    path: string | null = null;
    allImage: string[] = [];
    imageSelect: string|null = null;
    created(){
        this.getAllImage();
        this.allImage.forEach((value, index) => {
           this.allImage[index] = require(`@/assets/gallery/${this.path}/${value.replace('./', '')}`);
        });
        this.imageSelect = this.allImage[0];

        document.addEventListener('keydown', this.changePicture);
    }

    getAllImage(){
        switch(this.enumGallery){
            case Galleries.TENYEARS:
                this.allImage = require.context('@/assets/gallery/10years').keys();
                this.path = '10years';
                break;
        }
    }

    destroyed(){
        document.removeEventListener('keydown', this.changePicture);
    }

    onFocus(image: string){
        return{
            'focus': image == this.imageSelect
        }
    }

    changeImgSelect(index: number){
        this.imageSelect = this.allImage[index];
    }

    changePicture(event: KeyboardEvent){
        if(event.key === 'ArrowLeft') {
            this.goToPreviousImg();
        }
        else if(event.key === 'ArrowRight'){
            this.goToNextImg();
        }
    }

    clickOnImg(event: MouseEvent){
        const divideWith = window.innerWidth /2;
        if(event.clientX < divideWith)
            this.goToPreviousImg();

        else
            this.goToNextImg();
    }

    goToPreviousImg() {
        if(this.imageSelect === null)
            return;
        let index = this.allImage.indexOf(this.imageSelect);
        if (index !== 0) {
            index--;
            this.imageSelect = this.allImage[index];
            const elScroller = this.$refs.scroller as Element;
            elScroller.scrollLeft -= this.getGap();
        }
    }

    goToNextImg(){
        if(this.imageSelect === null)
            return;
        let index = this.allImage.indexOf(this.imageSelect);
        if (index < this.allImage.length - 1 ) {
            index++;
            this.imageSelect = this.allImage[index];
            const elScroller = this.$refs.scroller as Element;
            elScroller.scrollLeft += this.getGap();
        }
    }

    getGap(){
        const focusPicture = document.getElementsByClassName('focus')[0];
        return focusPicture.clientWidth + 15;
    }
}
