







import {Component, Vue, Prop} from "vue-property-decorator";
import CnTitle from "@/components/commons/CnTitle.vue";
@Component({
    components: {CnTitle}
})
export default class CnServiceTitle extends Vue{
    @Prop() imgName!: string;
    @Prop() title!: string;
    get fullPath(){
        return require(`@/assets/icons/${this.imgName}.svg`);
    }
}
