














































import {Component, Vue} from "vue-property-decorator";
import GuestForm from "@/models/GuestForm";
import validator from "validator";
import Contact from '@/models/Contact';
import axiosCn from "@/axios/axiosCn";

@Component
export default class CnContactForm extends Vue{
    contact = new GuestForm();
    company = null;
    request: string|null = null;
    requestError: string|null = null;

    disabledSending = false;

    async submitContact(){
        try {
            this.disabledSending = true;
            if (this.checkForm()) {
                const contact = new Contact(this.contact.guestName, this.contact.guestFirstName, this.contact.guestEmail, this.company, this.request);
                const response = await axiosCn.post('/cn/contact', {contact})
                if(response.status == 200){
                    this.resetForm();
                    alert('Merci pour votre message. Nous revenons vers vous au plus vite');
                }
            } else {
                alert('Veuillez compléter l\'ensemble du formulaire');
            }
        }catch(error){
            //console.log(error);
        } finally {
            this.disabledSending = false;
        }
    }

    checkRequest(){
        if(this.request == null || validator.isEmpty(this.request)){
            this.requestError = 'Veuillez indiquer un message';
        }
        else{
            this.requestError = null;
        }
    }

    checkForm(): boolean{
        const checkOk = this.contact.checkGuest();
        this.checkRequest();
        return checkOk && !this.requestError;
    }

    resetForm(){
        this.contact.reset();
        this.company = null;
        this.request = null;
    }
}
