

























import {Component, Vue, Prop} from "vue-property-decorator";
import CnTitle from "../commons/CnTitle.vue";

@Component({
    components: {CnTitle}
})
export default class CnProject extends Vue {
    @Prop() imgLogoExtension!: string;
    @Prop() imgExtension1?: string;
    @Prop() imgExtension2?: string;
    @Prop() company!: string;

    get fullPathLogo(){
        return require(`@/assets/projects/${this.imgLogoExtension}`);
}

    get fullPathP1(){
        if(this.imgExtension1)
            return require(`@/assets/projects/${this.imgExtension1}`);
        return null;
    }

    get fullPathP2(){
        if(this.imgExtension2)
            return require(`@/assets/projects/${this.imgExtension2}`);
        return null;
    }
}
