






















































import {Component, Vue} from "vue-property-decorator";
import CnTitle from "@/components/commons/CnTitle.vue";

@Component({
        components: {CnTitle}
    })
    export default class CnAboutUs extends Vue {
        age = 0;
        associates = 0;
        projects = 0;

        mounted(){
            const dateNow: Date = new Date();
            const dateAnniversary: Date = new Date('1995-05-08');
            const dif = Math.abs(dateNow.getTime() - dateAnniversary.getTime());
            const birthday = new Date(dif);
            const birthdayYear = birthday.getFullYear() - 1970;
            this.upBirthday(birthdayYear);
            this.upAssociates();
            this.upProjects();

        }

        async upBirthday(birthdayYear: number){
            for(let i = 0; i < birthdayYear; i ++){
                this.age ++;
                await this.sleep(100);
            }
        }

        async upAssociates(){
            for(let i = 0; i < 15; i ++){
                this.associates ++;
                await this.sleep(100);
            }
        }

        async upProjects(){
            for(let i = 0; i < 500; i ++){
                this.projects += 4;
                await this.sleep(1);
            }
        }

        sleep(ms: number) {
            return new Promise(resolve => setTimeout(resolve, ms));
        }
    }
