













import {Component, Vue, Prop} from "vue-property-decorator";

@Component
export default class CnSolutionFile extends Vue {
    @Prop() fileUrl!: string;
    preview = false;
    successLoaded = false;

    get pathToPdf(){
        return `${process.env.BASE_URL}solutionsfiles/${encodeURIComponent(this.fileUrl)}#zoom=100`;
    }

    showHidePreview(){
        this.preview = !this.preview;
    }

    get btnTextPreview(){
        if(this.preview)
            return 'Fermer'
        else
            return 'Preview'
    }

    loaded(){
        this.successLoaded = true;
    }
}
