






import {Component, Vue, Prop} from "vue-property-decorator";

@Component
export default class CnTitle extends Vue {
    @Prop() paddingLeft?: number | null;

    get padding(){
        if(this.paddingLeft)
            return this.paddingLeft
        return 10;
    }

    get paddingStyle(){
        return{
            'padding-left': this.padding + '%'
        }
    }
}
